import inquirer from 'inquirer';
import app from '../app';
import TimelyAPI from '../timely';
import Command from './command';

export default new Command('login', 'Log into Timely', () => { }, (argv) => {
	console.log('Login with Timely: ', TimelyAPI.authorizeURL());
	inquirer.prompt([{
		name: 'token',
		message: 'Authorization code: '
	}]).then(({ token }) => {
		TimelyAPI.authorize(token).finally((response) => {
			callback(response);
		});
	});
});
